import Boom from '@hapi/boom';
import UsersModel from '../models/user';

export default async function mongoAuthenticate(userId, password) {
  const Users = UsersModel();
  let user = await Users.findOne({ userId });

  if (!user) throw Boom.badRequest('Invalid username');

  user = await Users.findOne({ userId, password });

  if (!user) throw Boom.badRequest('Invalid password');
}
